import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { Search, Filter, TrendingUp, Heart, Music, Calendar, User, Share2 } from 'lucide-react';
import api from '../utils/api';

const PublicPlaylists = () => {
  const [playlists, setPlaylists] = useState([]);
  const [loading, setLoading] = useState(true);
  const [searchTerm, setSearchTerm] = useState('');
  const [sortBy, setSortBy] = useState('trending'); // trending, recent, popular, name
  const [selectedGenre, setSelectedGenre] = useState('all');
  const [genres] = useState([
    'all', 'pop', 'rock', 'hip-hop', 'jazz', 'classical', 'electronic', 
    'country', 'r&b', 'indie', 'folk', 'reggae', 'blues', 'metal'
  ]);

  useEffect(() => {
    fetchPublicPlaylists();
  }, []);

  const fetchPublicPlaylists = async () => {
    try {
      setLoading(true);
      const response = await api.get('/api/playlists/public');
      setPlaylists(response.data);
    } catch (error) {
      console.error('Error fetching public playlists:', error);
    } finally {
      setLoading(false);
    }
  };

  const likePlaylist = async (playlistId) => {
    try {
      await api.post(`/api/playlists/${playlistId}/like`);
      
      // Update the playlist in the state
      setPlaylists(playlists.map(playlist => {
        if (playlist._id === playlistId) {
          const currentUserLiked = playlist.userLiked;
          return {
            ...playlist,
            likes: currentUserLiked 
              ? playlist.likes?.filter(like => like !== 'currentUser') || []
              : [...(playlist.likes || []), 'currentUser'],
            userLiked: !currentUserLiked
          };
        }
        return playlist;
      }));
    } catch (error) {
      console.error('Error liking playlist:', error);
      alert('Failed to like playlist');
    }
  };

  const filteredAndSortedPlaylists = playlists
    .filter(playlist => {
      // Search filter
      const matchesSearch = playlist.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
                           playlist.moodPrompt?.toLowerCase().includes(searchTerm.toLowerCase()) ||
                           playlist.creator?.firstName?.toLowerCase().includes(searchTerm.toLowerCase());
      
      // Genre filter (basic implementation - in real app, you'd store genre data)
      const matchesGenre = selectedGenre === 'all' || 
                          playlist.moodPrompt?.toLowerCase().includes(selectedGenre) ||
                          playlist.name.toLowerCase().includes(selectedGenre);
      
      return matchesSearch && matchesGenre;
    })
    .sort((a, b) => {
      switch (sortBy) {
        case 'popular':
          return (b.likes?.length || 0) - (a.likes?.length || 0);
        case 'name':
          return a.name.localeCompare(b.name);
        case 'recent':
          return new Date(b.createdAt) - new Date(a.createdAt);
        case 'trending':
        default:
          // Trending: combination of likes and recency
          const aScore = (a.likes?.length || 0) + (Date.now() - new Date(a.createdAt)) / (1000 * 60 * 60 * 24);
          const bScore = (b.likes?.length || 0) + (Date.now() - new Date(b.createdAt)) / (1000 * 60 * 60 * 24);
          return bScore - aScore;
      }
    });

  if (loading) {
    return (
      <div className="max-w-6xl mx-auto">
        <div className="animate-pulse space-y-6">
          <div className="h-8 bg-gray-200 rounded w-1/3"></div>
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
            {[...Array(9)].map((_, i) => (
              <div key={i} className="bg-gray-200 rounded-xl h-80"></div>
            ))}
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="max-w-6xl mx-auto">
      {/* Header */}
      <div className="mb-8">
        <h1 className="text-3xl font-bold text-gray-900 mb-2">Discover Public Playlists</h1>
        <p className="text-gray-600">
          Explore playlists created by the community and find your next favorite songs
        </p>
      </div>

      {/* Controls */}
      <div className="bg-white rounded-lg shadow-sm p-6 mb-8">
        <div className="flex flex-col lg:flex-row lg:items-center lg:justify-between space-y-4 lg:space-y-0">
          {/* Search */}
          <div className="relative flex-1 max-w-md">
            <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400 w-5 h-5" />
            <input
              type="text"
              placeholder="Search playlists, moods, or creators..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              className="w-full pl-10 pr-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-primary-500 focus:border-transparent"
            />
          </div>

          <div className="flex flex-col sm:flex-row sm:items-center space-y-4 sm:space-y-0 sm:space-x-4">
            {/* Genre Filter */}
            <div className="flex items-center space-x-2">
              <Filter className="w-4 h-4 text-gray-500" />
              <select
                value={selectedGenre}
                onChange={(e) => setSelectedGenre(e.target.value)}
                className="border border-gray-300 rounded-lg px-3 py-2 text-sm focus:ring-2 focus:ring-primary-500 focus:border-transparent capitalize"
              >
                {genres.map(genre => (
                  <option key={genre} value={genre} className="capitalize">
                    {genre === 'all' ? 'All Genres' : genre.replace('-', ' ')}
                  </option>
                ))}
              </select>
            </div>

            {/* Sort */}
            <select
              value={sortBy}
              onChange={(e) => setSortBy(e.target.value)}
              className="border border-gray-300 rounded-lg px-3 py-2 text-sm focus:ring-2 focus:ring-primary-500 focus:border-transparent"
            >
              <option value="trending">Trending</option>
              <option value="popular">Most Liked</option>
              <option value="recent">Recently Created</option>
              <option value="name">Name</option>
            </select>
          </div>
        </div>
      </div>

      {/* Featured/Trending Section */}
      {sortBy === 'trending' && filteredAndSortedPlaylists.length > 0 && (
        <div className="mb-8">
          <div className="flex items-center space-x-2 mb-4">
            <TrendingUp className="w-5 h-5 text-orange-500" />
            <h2 className="text-xl font-semibold text-gray-900">Trending This Week</h2>
          </div>
          
          <div className="bg-gradient-to-r from-orange-500 to-pink-500 rounded-xl p-6 text-white">
            {filteredAndSortedPlaylists.slice(0, 1).map(playlist => (
              <div key={`featured-${playlist._id}`} className="flex items-center space-x-6">
                <div className="w-20 h-20 bg-white bg-opacity-20 rounded-xl flex items-center justify-center">
                  <Music className="w-10 h-10" />
                </div>
                
                <div className="flex-1">
                  <Link 
                    to={`/playlist/${playlist._id}`}
                    className="hover:underline"
                  >
                    <h3 className="text-2xl font-bold mb-2">{playlist.name}</h3>
                  </Link>
                  
                  {playlist.moodPrompt && (
                    <p className="text-orange-100 mb-2">"{playlist.moodPrompt}"</p>
                  )}
                  
                  <div className="flex items-center space-x-4 text-sm">
                    <div className="flex items-center space-x-1">
                      <User className="w-4 h-4" />
                      <span>{playlist.creator?.firstName || 'Anonymous'}</span>
                    </div>
                    <div className="flex items-center space-x-1">
                      <Heart className="w-4 h-4" />
                      <span>{playlist.likes?.length || 0} likes</span>
                    </div>
                    <div className="flex items-center space-x-1">
                      <Music className="w-4 h-4" />
                      <span>{playlist.tracks?.length || 0} tracks</span>
                    </div>
                  </div>
                </div>

                <button
                  onClick={() => likePlaylist(playlist._id)}
                  className={`p-3 rounded-full transition-colors ${
                    playlist.userLiked 
                      ? 'bg-red-500 text-white' 
                      : 'bg-white bg-opacity-20 hover:bg-opacity-30'
                  }`}
                >
                  <Heart className={`w-6 h-6 ${playlist.userLiked ? 'fill-current' : ''}`} />
                </button>
              </div>
            ))}
          </div>
        </div>
      )}

      {/* Empty State */}
      {filteredAndSortedPlaylists.length === 0 && (
        <div className="text-center py-16">
          <div className="w-24 h-24 bg-gray-100 rounded-full flex items-center justify-center mx-auto mb-6">
            <Music className="w-12 h-12 text-gray-400" />
          </div>
          <h3 className="text-xl font-semibold text-gray-900 mb-2">No playlists found</h3>
          <p className="text-gray-600 mb-6">
            Try adjusting your search or filters, or check back later for new playlists
          </p>
          <Link
            to="/generate"
            className="inline-flex items-center space-x-2 bg-primary-600 text-white px-6 py-3 rounded-lg font-semibold hover:bg-primary-700 transition-colors"
          >
            <Music className="w-5 h-5" />
            <span>Create a Playlist</span>
          </Link>
        </div>
      )}

      {/* Playlists Grid */}
      {filteredAndSortedPlaylists.length > 0 && (
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {filteredAndSortedPlaylists.slice(sortBy === 'trending' ? 1 : 0).map((playlist) => (
            <div key={playlist._id} className="bg-white rounded-xl shadow-sm hover:shadow-md transition-shadow group">
              {/* Playlist Cover */}
              <div className="relative">
                <div className="w-full h-48 bg-gradient-to-br from-primary-400 to-purple-500 rounded-t-xl flex items-center justify-center">
                  <Music className="w-16 h-16 text-white" />
                </div>
                
                {/* Like Button Overlay */}
                <div className="absolute top-3 right-3">
                  <button
                    onClick={() => likePlaylist(playlist._id)}
                    className={`w-10 h-10 rounded-full flex items-center justify-center shadow-lg transition-all ${
                      playlist.userLiked 
                        ? 'bg-red-500 text-white scale-110' 
                        : 'bg-white text-gray-600 hover:bg-red-50 hover:text-red-500'
                    }`}
                  >
                    <Heart className={`w-5 h-5 ${playlist.userLiked ? 'fill-current' : ''}`} />
                  </button>
                </div>

                {/* Public Badge */}
                <div className="absolute bottom-3 left-3">
                  <div className="flex items-center space-x-1 bg-green-600 text-white text-xs px-2 py-1 rounded-full">
                    <Share2 className="w-3 h-3" />
                    <span>Public</span>
                  </div>
                </div>
              </div>

              {/* Content */}
              <div className="p-6">
                <Link 
                  to={`/playlist/${playlist._id}`}
                  className="block hover:text-primary-600 transition-colors"
                >
                  <h3 className="font-semibold text-gray-900 mb-2 line-clamp-2">
                    {playlist.name}
                  </h3>
                </Link>
                
                {playlist.moodPrompt && (
                  <p className="text-sm text-gray-600 mb-3 line-clamp-2">
                    "{playlist.moodPrompt}"
                  </p>
                )}

                {/* Creator */}
                <div className="flex items-center space-x-2 mb-3">
                  <div className="w-6 h-6 bg-gray-300 rounded-full flex items-center justify-center">
                    <User className="w-4 h-4 text-gray-600" />
                  </div>
                  <span className="text-sm text-gray-700">
                    {playlist.creator?.firstName || 'Anonymous'}
                  </span>
                </div>

                {/* Stats */}
                <div className="flex items-center justify-between text-sm text-gray-500">
                  <div className="flex items-center space-x-4">
                    <span>{playlist.tracks?.length || 0} tracks</span>
                    <div className="flex items-center space-x-1">
                      <Heart className="w-4 h-4" />
                      <span>{playlist.likes?.length || 0}</span>
                    </div>
                  </div>
                  
                  <div className="flex items-center space-x-1">
                    <Calendar className="w-4 h-4" />
                    <span>{new Date(playlist.createdAt).toLocaleDateString()}</span>
                  </div>
                </div>
              </div>
            </div>
          ))}
        </div>
      )}

      {/* Load More */}
      {filteredAndSortedPlaylists.length > 12 && (
        <div className="text-center mt-12">
          <button className="px-8 py-3 bg-gray-100 text-gray-700 rounded-lg hover:bg-gray-200 transition-colors">
            Load More Playlists
          </button>
        </div>
      )}

      {/* Popular Moods Section */}
      <div className="mt-16 bg-gradient-to-r from-blue-50 to-purple-50 rounded-xl p-8">
        <h2 className="text-2xl font-bold text-gray-900 mb-6 text-center">
          Popular Moods This Week
        </h2>
        
        <div className="grid grid-cols-2 md:grid-cols-4 lg:grid-cols-6 gap-4">
          {[
            { mood: 'Chill Vibes', count: '2.1k', color: 'bg-blue-100 text-blue-700' },
            { mood: 'Workout Energy', count: '1.8k', color: 'bg-orange-100 text-orange-700' },
            { mood: 'Study Focus', count: '1.5k', color: 'bg-green-100 text-green-700' },
            { mood: 'Nostalgic', count: '1.2k', color: 'bg-purple-100 text-purple-700' },
            { mood: 'Party Time', count: '987', color: 'bg-pink-100 text-pink-700' },
            { mood: 'Romantic', count: '834', color: 'bg-red-100 text-red-700' }
          ].map((item, index) => (
            <button
              key={index}
              onClick={() => setSearchTerm(item.mood.toLowerCase())}
              className={`p-4 rounded-lg text-center hover:scale-105 transition-transform ${item.color}`}
            >
              <div className="font-semibold">{item.mood}</div>
              <div className="text-sm opacity-75">{item.count} playlists</div>
            </button>
          ))}
        </div>
      </div>
    </div>
  );
};

export default PublicPlaylists;